.App {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	width: 100%;
	height: 100vh;
	max-width: 1440px;
	margin: 0px auto;
}

.app-container {
	width: 100%;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
/*	border: 1px solid black;*/
}

@media only screen and (max-width: 500px) {
	.App {
		max-width: 375px;
		justify-content: flex-start;
	}
}