#top-section {
	color: white;
	text-align: center;
	background-repeat: round;
/*	background-position: center;*/
	background-image: url("../../../public/images/pattern-bg.png");
	padding-top: 30px;
	height: 250px;
	width: 100%;
}

#top-section-title {
	margin: 0;
	margin-bottom: 25px;
}

#top-section-input {
	border-radius: 10px;
	max-width: 555px;
	margin: 0px auto;
	height: 60px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: white;
	cursor: pointer;
}

#top-section-input input {
	height: 90%;
	margin: 0px auto;
	display: inline-block;
	width: 84%;
	border: none;
	outline: none;
	color: hsl(0, 0%, 17%);
	cursor: pointer;
	font-size: 18px;
}

#top-section-input-button {
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: black;
	width: 60px;
	height: 100%;
	border-radius: 0px 9px 9px 0px;
	cursor: pointer;
}

#top-section-input-button:hover {
	background-color: hsl(0, 0%, 17%);
}

@media only screen and (max-width: 500px) {
	#top-section {
		height: 28vh;
	}

	#top-section-input {
		max-width: 325px;
	}

	#top-section-input input {
		max-width: 245px;
		font-size: 15px;
	}
}