#info-section {
	display: flex;
	width: 98%;
	max-width: 1005px;
	height: 150px;
	align-items: center;
	justify-content: space-around;
	position: absolute;
	background-color: white;
	font-weight: 500;
	border-radius: 15px;
	z-index: 2;
	font-size: 20px;
	top: 200px;
}

.info-section-items {
	width: 200px;
	height: 100px;
	margin: 5px;
}

.info-section-items-title {
	color: hsl(0, 0%, 59%);
	letter-spacing: 2px;
	font-size: 13px;
	margin-bottom: 10px;
}

.info-section-items-content {
	color: black;
}

.info-ip {
	box-shadow: 1px 0px hsl(0, 0%, 59%);
}

.info-location {
	box-shadow: 1px 0px hsl(0, 0%, 59%);
}

.info-timezone {
	box-shadow: 1px 0px hsl(0, 0%, 59%);
}

.info-isp {

}

@media only screen and (max-width: 500px) {
	#info-section {
		max-width: 330px;
		flex-direction: column;
		height: 294px;
		justify-content: center;
		top: 170px;
	}

	.info-section-items {
		width: 100%;
		text-align: center;
/*		border: 1px solid black;*/
	}

	.info-section-items-title {
		font-size: 10px;
		font-weight: 600;
	}

	.info-ip, .info-location, .info-timezone {
		box-shadow: none;
	}
}